.contents-Skills {
  padding: 50px 15px;
  border-bottom: 1px solid #dcd9d9;
  text-align: center;
}

.contents-Skills h2 {
  color: #374054;
  margin-bottom: 50px;
}

.Skills-heading {
  position: relative;
  display: inline-block;
  font-size: 2em;
  font-weight: 300;
  margin: 0 0 30px 0;
  padding-bottom: 10px;
}

.Skills-heading:after {
  position: absolute;
  content: '';
  top: 100%;
  height: 1px;
  width: 50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: #3498db;
}

.contents-Skills ul {
  display: block;
  margin: 0 auto;
  padding: 0;
  max-width: 800px;
}

.contents-Skills li {
  display: inline-block;
  margin: 7px;
  padding: 5px 10px;
  color: #374054;
  background: #e4e4ea;
  list-style: none;
  cursor: default;
  font-size: 1.2em;
}